import React, { useState, useEffect, useMemo } from "react";
import s from "./Admin.module.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import BreadCrumbs from "../../misc/BreadCrumbs/BreadCrumbs";
import {
  faHome,
  faPencilAlt,
  faPlus,
  faSignOutAlt,
} from "@fortawesome/free-solid-svg-icons";
import FixedWrapper from "../../wrappers/FixedWrapper/FixedWrapper";
import { TabList, Tabs, Tab, TabPanel } from "react-tabs";
import OrderCard from "../../misc/Admin/OrderCard/OrderCard";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import NewsAdminCard from "../../misc/Admin/NewsAdminCard/NewsAdminCard";
import Button from "../../misc/Button/Button";
import UserCard from "../../misc/Admin/UserCard/UserCard";
import OrderProductCard from "../../misc/OrderProductCard/OrderProductCard";
import {
  addToCartAction,
  removeFromCartAction,
} from "../../store/actions/cartActions";
import Input from "../../misc/Inputs/Input/Input";
import { Formik } from "formik";
import Select from "../../misc/Select/Select";
import AdminRow from "../../misc/Admin/AdminRow/AdminRow";
import {
  getProducts,
  getCategoriesAction,
} from "../../store/actions/productsActions";
import {
  createCategoryAction,
  getVendorsAction,
  createVendorAction,
  getAttributesAction,
  deleteAttributeAction,
} from "../../store/actions/adminActions";

const Admin = ({
  recentNews,
  allProducts,
  categories,
  attributes,
  getCategories,
  getVendors,
  getAttributes,
  createCategory,
  createVendor,
  deleteAttribute,
}) => {
  const [activeTabIndex, setActiveTabIndex] = useState(0);
  const breadCrumbsItems = [
    {
      name: "",
      path: "/",
      icon: <FontAwesomeIcon icon={faHome} />,
    },
    { name: "", path: "/admin" },
  ];

  useEffect(() => {
    getCategories();
    getVendors();
    getAttributes();
  }, []);

  console.log("categories ===", categories);

  return (
    <div className={s.container}>
      <div className={s.title__container}>
        <h4 className={s.title}></h4>

        <BreadCrumbs items={breadCrumbsItems} />
      </div>
      <FixedWrapper>
        <Tabs>
          <TabList className={s.tabs}>
            {[
              "",
              "",
              "",
              "",
              "",
              "",
              "",
              " ",
            ].map((item, i) => (
              <Tab
                onClick={() => setActiveTabIndex(i)}
                key={i}
                className={
                  activeTabIndex === i ? `${s.tab} ${s.tab__active}` : s.tab
                }
              >
                {item}
              </Tab>
            ))}
            <div>
              <Button title="  " className={s.logout__button}>
                <FontAwesomeIcon
                  className={s.logout__icon}
                  icon={faSignOutAlt}
                />
              </Button>
            </div>
          </TabList>
          <TabPanel>
            <Link to="/admin/create-order">
              <Button className={s.add__button} title=" ">
                <FontAwesomeIcon icon={faPlus} className={s.add__more__icon} />
              </Button>
            </Link>
            <div className={s.order__header}>
              <span> </span>
              <span> </span>
              <span></span>
              <span> </span>
              <span> </span>
              <span> </span>
            </div>
            <Link to="/admin/edit-order/:id">
              <OrderCard />
            </Link>
            <OrderCard />
            <OrderCard />
            <OrderCard />
            <OrderCard />
          </TabPanel>
          <TabPanel>
            <Link to="/admin/create-product">
              <Button title=" ">
                <FontAwesomeIcon icon={faPlus} className={s.add__more__icon} />
              </Button>
            </Link>
            <div className={s.products__container}>
              {allProducts.map((product) => (
                <Link
                  key={product._id}
                  to={`/admin/edit-product/${product._id}`}
                >
                  <OrderProductCard
                    isSmall
                    {...{ product }}
                    key={product._id}
                    className={s.container__}
                  />
                </Link>
              ))}
            </div>
          </TabPanel>
          <TabPanel>
            <Formik
              initialValues={{
                name: "",
                parentCategory: null,
                parentCategoryLabel: null,
              }}
              onSubmit={({ name, parentCategory }, { resetForm }) => {
                const categoryToSubmit = {
                  parentID: parentCategory._id,
                  subParentID: null,
                  title: name,
                };
                if (parentCategory.parent?.length) {
                  categoryToSubmit.parent = parentCategory.parent;
                  categoryToSubmit.sub = [
                    { title: parentCategory.title, _id: parentCategory._id },
                  ];
                }
                createCategory(categoryToSubmit);
                resetForm({
                  name: "",
                  parentCategory: null,
                  parentCategoryLabel: null,
                });
              }}
            >
              {({
                handleChange,
                handleBlur,
                handleSubmit,
                values,
                setValues,
              }) => {
                console.log("example category ===", categories[1]);

                console.log("values ===", values);

                return (
                  <form
                    onSubmit={handleSubmit}
                    onKeyDown={(e) => {
                      if (e.key === "Enter") {
                        handleSubmit();
                      }
                    }}
                  >
                    <div className={s.add__category__container}>
                      <Input
                        name="name"
                        label=" "
                        placeholder=""
                        value={values.name}
                        containerClass={s.add__category__input__container}
                        inputClass={s.add__category__input}
                        onChange={handleChange}
                        onBlur={handleSubmit}
                      />
                      <Select
                        label="  "
                        containerClass={s.add__category__select__container}
                        // withSearch
                        // noDefaultValue
                        value={values.parentCategoryLabel}
                        onSelect={(selectedOption) => {
                          setValues({
                            ...values,
                            parentCategory: selectedOption.value,
                            parentCategoryLabel: selectedOption.label,
                          });
                          console.log("value ===", selectedOption);
                        }}
                        options={categories
                          .filter(({ sub }) => !sub.length)
                          .map(({ title, parent, _id }) => {
                            const isParentVisible = parent.length;
                            return {
                              label: `${title}${
                                isParentVisible
                                  ? `,   - ${parent[0].title}`
                                  : ""
                              }`,
                              value: {
                                title,
                                _id,
                                parent,
                              },
                            };
                          })}
                      />
                    </div>
                    <Button
                      onClick={handleSubmit}
                      type="submit"
                      className={s.add__button}
                      title=" "
                    >
                      <FontAwesomeIcon
                        className={s.add__more__icon}
                        icon={faPlus}
                      />
                    </Button>
                  </form>
                );
              }}
            </Formik>
            <div className={s.order__header}>
              <span></span>
              <span>  </span>
            </div>

            {categories &&
              categories.map((category) => (
                <div className={s.category__container} key={category._id}>
                  <p className={s.category}>{category.title}</p>
                  {!!category.parent.length && !category.sub.length && (
                    <p className={s.category}>{category.parent[0].title}</p>
                  )}
                  {!!category.sub.length && (
                    <p className={s.category}>{category.sub[0].title}</p>
                  )}
                </div>
              ))}
          </TabPanel>
          <TabPanel>
            {attributes.map(({ _id, name }) => (
              <AdminRow
                key={_id}
                title={name}
                onEdit={() => {}}
                onDelete={() => deleteAttribute(_id)}
              />
            ))}
          </TabPanel>
          <TabPanel>
            <Formik
              initialValues={{
                title: "",
              }}
              onSubmit={({ title }) => {
                createVendor({ title });
              }}
            >
              {({ handleChange, handleSubmit, values }) => {
                return (
                  <div>
                    <div className={s.seller__container}>
                      <Input
                        name="title"
                        label=" "
                        placeholder="Germany"
                        value={values.title}
                        containerClass={s.add__category__input__container}
                        inputClass={s.add__category__input}
                        onChange={handleChange}
                      />
                    </div>
                    <Button
                      onClick={handleSubmit}
                      className={s.add__button}
                      title=" "
                      type="submit"
                    >
                      <FontAwesomeIcon
                        className={s.add__more__icon}
                        icon={faPlus}
                      />
                    </Button>
                  </div>
                );
              }}
            </Formik>
            <div className={s.order__header}>
              <span></span>
              <span> </span>
            </div>
            {/* <EditSeller />
            <EditSeller />
            <EditSeller />
            <EditSeller />
            <EditSeller />
            <EditSeller /> */}
          </TabPanel>
          <TabPanel>
            <div className={s.order__header}>
              <span>'</span>
              <span></span>
              <span></span>
              <span> </span>
              <span> </span>
            </div>
            <div className={s.create__container}>
              <Link to="/admin/create-user">
                <Button title=" " className={s.create__btn}>
                  <FontAwesomeIcon
                    icon={faPlus}
                    className={s.add__more__icon}
                  />
                </Button>
              </Link>
            </div>
            {/* {recentNews.map((newsItem, i) => (
              <NewsAdminCard {...{ newsItem }} key={newsItem._id} />
            ))} */}
            <UserCard />
            <UserCard />
            <UserCard />
            <UserCard />
          </TabPanel>
          {/* <TabPanel>123</TabPanel> */}
          <TabPanel>
            <div className={s.order__header}>
              <span></span>
              <span></span>
              <span> </span>
            </div>
            <div className={s.create__container}>
              <Link to="/admin/create-news">
                <Button title=" " className={s.create__btn}>
                  <FontAwesomeIcon
                    icon={faPlus}
                    className={s.add__more__icon}
                  />
                </Button>
              </Link>
            </div>
            <div className={s.section}>
              {/* <h3 className={s.section__title}></h3> */}
              <div className={s.news__container}>
                {recentNews.map((newsItem, i) => (
                  <NewsAdminCard {...{ newsItem }} key={newsItem._id} />
                ))}
              </div>
            </div>
          </TabPanel>
          <TabPanel>123</TabPanel>
        </Tabs>
      </FixedWrapper>
    </div>
  );
};
const mapStateToProps = (state) => {
  return {
    allProducts: state.products.all,
    recentNews: state.news.recent,
    cartProducts: state.cart.all,
    categories: state.products.categories,
    attributes: state.admin.attributes,
    // fullPrice: state.cart.fullPrice,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    addToCart: (product) => dispatch(addToCartAction(product)),
    removeFromCart: (product) => dispatch(removeFromCartAction(product)),
    getProducts: () => dispatch(getProducts()),
    getCategories: () => dispatch(getCategoriesAction()),
    getVendors: () => dispatch(getVendorsAction()),
    getAttributes: () => dispatch(getAttributesAction()),
    deleteAttribute: (id) => dispatch(deleteAttributeAction(id)),
    createCategory: (category) => dispatch(createCategoryAction(category)),
    createVendor: (vendor) => dispatch(createVendorAction(vendor)),
    // setFullPrice: (fullPrice) => dispatch(setFullPriceAction(fullPrice)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Admin);
